<template>
    <div class="markdown-editor">
        <textarea></textarea>
    </div>
</template>

<script>
    import SimpleMDE from 'simplemde';
    import marked from 'marked';
    import { Textcomplete } from 'textcomplete';
    import CodeMirrorEditor from 'textcomplete.codemirror';

    export default {
        name: 'markdown-input',
        props: {
            value: String,
            previewClass: String,
            autoinit: {
                type: Boolean,
                default() {
                    return true;
                },
            },
            highlight: {
                type: Boolean,
                default() {
                    return false;
                },
            },
            sanitize: {
                type: Boolean,
                default() {
                    return false;
                },
            },
            configs: {
                type: Object,
                default() {
                    return {};
                },
            },
        },
        mounted() {
            if (this.autoinit) this.initialize();
        },
        activated() {
            const editor = this.simplemde;
            if (!editor) return;
            const isActive = editor.isSideBySideActive() || editor.isPreviewActive();
            if (isActive) editor.toggleFullScreen();
        },
        methods: {
            initialize() {
                const configs = Object.assign({
                    element: this.$el.firstElementChild,
                    initialValue: this.value,
                    renderingConfig: {},
                    toolbar: ["bold", "italic", "strikethrough", "|", "quote", "unordered-list", "ordered-list", "|", "link", "horizontal-rule", "|", "side-by-side", "preview", "fullscreen"],
                }, this.configs);

                // Initial Input
                if (configs.initialValue) {
                    this.$emit('input', configs.initialValue);
                }

                // Should we highlight code
                if (this.highlight) {
                    configs.renderingConfig.codeSyntaxHighlighting = true;
                }

                // sanitize html?
                marked.setOptions({ sanitize: this.sanitize });

                // Set simplemde
                this.simplemde = new SimpleMDE(configs);

                const textEditor = new CodeMirrorEditor(this.simplemde.codemirror);
                const textcomplete = new Textcomplete(textEditor);
                textcomplete.register([{
                    // User match
                    match: /(^|\s)@(\w.+)$/,
                    search: function (term, callback) {
                        callback(function() { return term })
                    },
                    replace: function (value) {
                        return '@' + value;
                    }
                }, {
                    // Ticket match
                    match: /(^|\s)#(\w.+)$/,
                    search: function (term, callback) {
                        callback(function() { return term })
                    },
                    replace: function (value) {
                        return '#' + value;
                    }
                }, {
                    // KB Match
                    match: /(^|\s)!(\w.+)$/,
                    search: function (term, callback) {
                        callback(function() { return term })
                    },
                    replace: function (value) {
                        return '!' + value;
                    }
                }]);

                // set previewClass
                const className = this.previewClass || '';
                this.addPreviewClass(className);

                // Bind events
                this.simplemde.codemirror.on("change", function (instance, changeObj) {
                    textcomplete.trigger(this.simplemde.value());
                });
                this.bindingEvents();
            },
            bindingEvents() {
                this.simplemde.codemirror.on('change', () => {
                    this.$emit('input', this.simplemde.value());
                });
            },
            addPreviewClass(className) {
                const wrapper = this.simplemde.codemirror.getWrapperElement();
                const preview = document.createElement('div');
                wrapper.nextSibling.className += ` ${className}`;
                preview.className = `editor-preview ${className}`;
                wrapper.appendChild(preview);
            },
        },
        destroyed() {
            this.simplemde = null;
        },
        watch: {
            value(val) {
                if (val === this.simplemde.value()) return;
                this.simplemde.value(val);
            },
        },
    };
</script>

<style>
    @import '~simplemde/dist/simplemde.min.css';

    .markdown-editor .markdown-body {
        padding: 0.5em
    }

    .markdown-editor .editor-preview-active, .markdown-editor .editor-preview-active-side {
        display: block;
    }
</style>


